@import "../../styles/variables.scss";

footer {
    background-color: $secundary;
    color: $text-secundary;
    font-size: .8rem;
    padding: 1rem;
}

footer > p {
    text-align: center;
}

footer > ul {
    list-style-type: none;
    display: flex;
    flex-wrap: wrap;
    
}

footer > hr {
    margin-bottom: 2rem;
    margin-top: 2rem;
}

footer > ul > li {
    flex: 1 1 20rem;
    margin: .5rem; 
}

footer .icons{
    color: $primary;
    background-color:  $secundary;
}





